.carousel-item .img-fluid {
    width:600px;
    height: 460px;

}
.img-fluid::before{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 20em;
    height: 20em;
    background: rgba(255, 147, 58, 0.6);
}

.test {
    position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
  font-weight: bolder;
  }
  
  .image {
    width: 100%;
    height: 100%;
  }
  
  .test:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;

    width: 100%;
    height: 460px;
    background: rgba(0, 0, 0, 0.6);
  }
  .text {
    font-size: 2em;
    color: white;
    background-color: #fc5012;
    padding:3px;
    text-shadow: 0.1em 0.1em 0.5em rgba(0,0,0,0.5);
    margin: 0;
    position: absolute;
  }
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
}

.card {
    background: white;
    color: #a9ded8;
    border: none;
}

.register-box {
    padding: 10px;
}

.form-content .form-box label {
    color: black;
    display: block;
    font-size: 18px;
    letter-spacing: 2px;
    margin-bottom: 5px;
}

.form-content .form-box input {
    background: #000000;
    border: none;
    box-shadow: inset -3px -3px 6px rgba(255, 255, 255, 0.1),
        inset 3px 3px 6px rgba(0, 0, 0, 0.8);
    color: #6f6f6f;
    font-size: 18px;
    height: 50px;
    outline: none;
    padding: 5px 15px;
    width: 100%;
}

.form-content .form-box input:focus {
    text-shadow: 0 0 10px #fc5012;
    color: #fc5012;
}

.form-content .form-box input[type="submit"] {
    box-shadow: -3px -3px 6px rgba(255, 255, 255, 0.1),
        3px 3px 6px rgba(0, 0, 0, 0.8);
    font-weight: 600;
    margin-top: 20px;
}

.form-content .form-box input[type="submit"]:active {
    box-shadow: 0 0 20px #fc5012;
    background: #fc5012;
    color: #091921;
    margin-top: 20px;
}

.form-content .form-box input::-webkit-input-placeholder {
    color: #6f6f6f;
    text-shadow: none !important;
    font-size: 18px;
}

.form-content .form-box input:-ms-input-placeholder {
    color: #6f6f6f;
    text-shadow: none !important;
    font-size: 18px;
}

.form-content .form-box input::placeholder {
    color: #6f6f6f;
    text-shadow: none !important;
    font-size: 18px;
}

